@font-face {
  font-weight: 300;
  font-style: normal;
  font-family: "Manrope";
  font-display: swap;
  src: url("../fonts/ManropeLight.woff2") format("woff2"), url("../fonts/ManropeLight.woff") format("woff");
}
@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Manrope";
  font-display: swap;
  src: url("../fonts/ManropeRegular.woff2") format("woff2"), url("../fonts/ManropeRegular.woff") format("woff");
}
@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "BebasNeue";
  font-display: swap;
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2"), url("../fonts/BebasNeueRegular.woff") format("woff");
}

html {
  margin-right: calc(-1 * (100vw - 100%));
  box-sizing: border-box;
  font-family: "Manrope", sans-serif;
  overflow-x: hidden;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  box-sizing: border-box;
  background-color: #FFF;
}

html, body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "BebasNeue", sans-serif;
  color: #282828;
}

p, a, button, li {
  color: #282828;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
}
@media (max-width: 1440px) {
  .container {
    padding: 0 15px 0 15px;
  }
}

h1 {
  font-weight: 400;
  font-size: 64px;
  line-height: 72px;
  letter-spacing: 4.16px;
  text-transform: uppercase;
}
@media (max-width: 1540px) {
  h1 {
    font-size: 56px;
    line-height: 60px;
  }
}
@media (max-width: 991px) {
  h1 {
    font-size: 48px;
    line-height: 54px;
  }
}
@media (max-width: 370px) {
  h1 {
    font-size: 38px;
    line-height: 50px;
  }
}

h2 {
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 48px;
  letter-spacing: 3.25px;
  text-transform: uppercase;
}
@media (max-width: 1540px) {
  h2 {
    font-size: 50px -8;
  }
}
@media (max-width: 991px) {
  h2 {
    font-size: 36px;
    line-height: 36px;
  }
}

p {
  font-size: 16px;
}
@media (max-width: 768px) {
  p {
    font-size: 14px;
  }
}

a {
  text-decoration: none;
  font-size: 16px;
}
@media (max-width: 768px) {
  a {
    font-size: 14px;
  }
}

input {
  font-family: "Manrope", sans-serif;
  background: none;
  border: none;
  outline: none;
}

button {
  position: relative;
  font-family: "Manrope", sans-serif;
  cursor: pointer;
  outline: none;
  border: none;
  background: none;
  padding: 0;
  font-size: 16px;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
  max-width: fit-content;
}

textarea {
  resize: none;
  background: inherit;
  outline: none;
  border: none;
}

picture {
  display: flex;
}

.logo-picture {
  max-width: 300px;
}

.rotate {
  transform-origin: center;
}

.rotate-middle {
  animation: rotate-middle 60s linear infinite;
}

.rotate-top {
  animation: rotate-top 60s linear infinite;
}

.rotate-bottom {
  animation: rotate-bottom 60s linear infinite;
}

@keyframes rotate-bottom {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-360deg);
  }
}
@keyframes rotate-middle {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes rotate-top {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-360deg);
  }
}
.notification {
  position: relative;
  display: flex;
  align-items: center;
}
.notification:hover .notification__hidden {
  opacity: 1;
  visibility: visible;
  z-index: 10;
}
.notification:hover .notification__hidden--touch {
  pointer-events: auto;
}
@media (max-width: 991px) {
  .notification__text {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .notification__text {
    font-size: 12px;
  }
}
.notification__icon {
  max-width: 24px;
  max-height: 24px;
}
.notification__hidden {
  position: absolute;
  width: 270px;
  padding: 18px;
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 4px 31px 0px rgba(0, 0, 0, 0.07);
  left: calc(100% + 10px);
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  pointer-events: none;
  transition: opacity 0.4s;
}
@media (max-width: 1540px) {
  .notification__hidden {
    left: -100px;
    top: 100%;
  }
}
@media (max-width: 991px) {
  .notification__hidden {
    left: calc(100% + 10px);
    top: 0;
  }
}
@media (max-width: 560px) {
  .notification__hidden {
    left: -100px;
    top: 100%;
  }
}
@media (max-width: 370px) {
  .notification__hidden--touch {
    left: -150px;
  }
}
.notification__hidden--touch:before {
  position: absolute;
  display: flex;
  content: "";
  width: 20px;
  height: 100%;
  top: 0;
  left: -20px;
}
.notification__hidden--form {
  top: calc(50% - 12px);
}
@media (max-width: 1540px) {
  .notification__hidden--form {
    top: 100%;
  }
}
@media (max-width: 991px) {
  .notification__hidden--form {
    top: calc(50% - 12px);
  }
}
@media (max-width: 560px) {
  .notification__hidden--form {
    top: 100%;
    left: -50px;
  }
}
@media (max-width: 370px) {
  .notification__hidden--form {
    left: -70px;
  }
}
.notification__link {
  display: block;
  margin-top: 8px;
  color: #008CCC;
  text-decoration-line: underline;
}
@media (max-width: 991px) {
  .notification__link {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .notification__link {
    font-size: 12px;
  }
}

table {
  width: 100%;
}

th {
  color: #282828;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.42px;
  padding: 10px;
}

